* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  width: 70vw;
  margin: auto;
}

input {
  width: 100%;
  margin-top: 8px;
  padding: 4px;
  margin-bottom: 8px;
  border-radius: 4px;
  font-size: 1.4rem;
} 
.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card {
  align-items: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  width: 250px;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
  width: 100%;
}
